export default {
	data () {
		return {
			ceshi: '测试列表',
			longitude: '',
			latitude: '',
			imgList: [],
			src: '',
		}
	},
	onLoad() {
	},
	methods: {
		getAuthorizeInfo (a="scope.userLocation") {
			var _this=this;
			uni.authorize({
				scope: a,
				success() { //1.1 允许授权
					_this.getLocationInfo();
				},
				fail(){    //1.2 拒绝授权
					uni.showModal({
						content:'你拒绝了授权，无法获得周边信息',
						confirmColor: '#1cc09f',
						showCancel: false,
					})
					// console.log("你拒绝了授权，无法获得周边信息")
				}
			})
		},
		getLocationInfo(){
			let that = this;
			uni.getLocation({
			    type: 'wgs84',
			    success: function (res) {
					that.longitude = res.longitude;
					that.latitude = res.latitude;

						uni.request({
							header:{
								"Content-Type": "application/text"
							},
							url:'http://apis.map.qq.com/ws/geocoder/v1/?location='+res.longitude.toString()+','+res.latitude.toString()+'&key=MVGBZ-R2U3U-W5CVY-2PQID-AT4VZ-PDF35',
							success(res) {
								console.log(res.data);
								if(res.statusCode===200){
									console.log("获取中文街道地理位置成功")
								}else{
									console.log("获取信息失败，请重试！")
								}
							}
						})

			    }
			});
		},
		isGetLocation(a="scope.userLocation"){
			let _this = this;
			uni.getSetting({
				fail(err) {
					console.log(err);
				},
				success(res) {
					if (!res.authSetting[a]) {  //3.1 每次进入程序判断当前是否获得授权，如果没有就去获得授权，如果获得授权，就直接获取当前地理位置
						_this.getAuthorizeInfo()
					}else{
						_this.getLocationInfo()
					}
				}
			})
		},
		takePhoto() {
			// const ctx = uni.createCameraContext();
			// ctx.takePhoto({
			// 	quality: 'high',
			// 	success: (res) => {
			// 		console.log(res)
			// 		this.src = res.tempImagePath
			// 	}
			// });
			let _this = this;
			uni.chooseImage({
				count: 4,
				sizeType:["original","compressed"],
				sourceType: ['album'],
				success: (res) => {
					// console.log(JSON.stringify(res.tempFilePaths));
					console.log(res.tempFilePaths[0]);
					this.src = res.tempFilePaths[0];
					// var tempFilePaths = res.tempFilePaths;
					// uni.saveFile({
					// 	tempFilePath: tempFilePaths[0],
					// 	success: function (res) {
					// 		var savedFilePath = res.savedFilePath;
					// 		_this.src = savedFilePath;
					// 	},
					// 	fail(error) {
					// 		console.log(error);
					// 	}
					// })
				}
			})
		},
		error(e) {
			console.log(e.detail);
		},
		ChooseImage() {
			uni.chooseImage({
				count: 4, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFilePaths)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			uni.showModal({
				title: '召唤师',
				content: '确定要删除这段回忆吗？',
				cancelText: '再看看',
				confirmText: '再见',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
	},
	onReady() {

			this.isGetLocation()




	},
	computed:{
		nowDate () {
			return new Date().getHours()
		}
	}
}
